.grid-container{
  display: grid;
  grid-template-areas: 
  "header"
  "main"
  "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
  
}

.header{
  grid-area:header ;
  background:linear-gradient( 90deg,#a925e2 , #c76fec,#a925e2);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  position:-webkit-sticky;
  position:sticky;
  top:.1rem;
  z-index: 2;

}
.cart-icon {
  position: relative;

}
.badge{
  background-color: #fd2020;
  color: white;
  border-radius: 50%;
  padding: .4rem .5rem .4rem .4rem;
  font-size: 1.4rem;
  margin: .2rem;
  position: absolute;
  right: .6rem;
  top: 1rem;
  
}


